<template>
  <div class="body">
  <div class="header">
    <Header />
  </div>

  <div class="main-layout">
    <h1>商品列表</h1>
    <!-- 顯示商品列表，即使尚未加載商品數據 -->
    <div v-if="allProducts.length > 0" class="mainnav">
      <div v-for="(product, index) in allProducts" :key="product.productID" class="product-layout">
        <div class="nave">
          <img :src="product.imgSrc" :alt="product.alt" class="imea" />
        <!-- <img v-lazy="product.imgSrc" :alt="product.alt" class="imea" />懶加載寫法 -->
          <span class="product-1">{{ product.productName }} - {{ product.price }}元</span>
        </div>
        <div class="product-2">
          <div class="product-3">
            <button @click="decreaseQuantity(index)" class="btn-1">-</button>
            <input type="number" class="inpt" v-model.number="product.purchaseQuantity" min="0" :max="product.stockQuantity" />
            <button @click="increaseQuantity(index)" class="btn-1">+</button>
          </div>
          <span class="tex2">購買數量: {{ product.purchaseQuantity }}</span>
        </div>
      </div>
    </div>

    <div class="total" v-if="allProducts.length > 0">
      <span>總購買數量: {{ totalPurchaseQuantity }}</span>
      <span>總價: {{ totalPrice }} 元</span>
    </div>
    <div class="bnt">
      <button @click="submitOrder" v-if="allProducts.length" class="btn-2">送出訂單</button>
    </div>
  </div>
</div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import { useStore } from 'vuex';
import Header from '@/components/shar/Header.vue';

const store = useStore();
const allProducts = ref([]);

onMounted(async () => {
  document.title = "商品列表 - 我的蛋糕店";

  allProducts.value = [
    { 
      productID: 1, 
      productName: "草莓藍文乳酪酸奶油卡士達", 
      price: 500, 
      purchaseQuantity: 0, 
      imgSrc: require('@/assets/9StrawberryBlueberryCustard(草莓藍文乳酪酸奶油卡士達)/513218.jpg'),
      alt: '草莓藍文乳酪酸奶油卡士達'
    }, 
    { 
      productID: 2, 
      productName: "咖啡可可草莓", 
      price: 600, 
      purchaseQuantity: 0, 
      imgSrc: require('@/assets/3CoffeeCocoaStrawberry(咖啡可可草莓)/518081.jpg'),
      alt: '咖啡可可草莓'
    },
    { 
      productID: 3, 
      productName: "抹茶蜜柚蘭姆栗子", 
      price: 450, 
      purchaseQuantity: 0, 
      imgSrc: require('@/assets/4MatchaCitrusPastry(抹茶蜜柚蘭姆栗子)/494007.jpg'),
      alt: '抹茶蜜柚蘭姆栗子'
    },
    { 
      productID: 4, 
      productName: "紅酒葡萄紅茶", 
      price: 350, 
      purchaseQuantity: 0, 
      imgSrc: require('@/assets/5RedWineGrapeTea(紅酒葡萄紅茶)/482134_0.jpg'),
      alt: '紅酒葡萄紅茶'
    },
    { 
      productID: 5, 
      productName: "栗香柿子紅茶", 
      price: 550, 
      purchaseQuantity: 0, 
      imgSrc: require('@/assets/6ChestnutPersimmonTea(栗香柿子紅茶)/482132.jpg'),
      alt: '栗香柿子紅茶'
    },
    { 
      productID: 6, 
      productName: "草莓太妃糖咖啡", 
      price: 400, 
      purchaseQuantity: 0, 
      imgSrc: require('@/assets/7StrawberryToffeeCoffee(草莓太妃糖咖啡)/513219.jpg'),
      alt: '草莓太妃糖咖啡'
    },
    { 
      productID: 7, 
      productName: "草莓酸奶油巧克力", 
      price: 700, 
      purchaseQuantity: 0, 
      imgSrc: require('@/assets/8StrawberryYogurtChocolate(草莓酸奶油巧克力)/528099.jpg'),
      alt: '草莓酸奶油巧克力'
    },
    { 
      productID: 8, 
      productName: "梨山烏龍酥餅", 
      price: 380, 
      purchaseQuantity: 0, 
      imgSrc: require('@/assets/11OolongBiscuit(梨山烏龍酥餅)/476235.jpg'),
      alt: '梨山烏龍酥餅'
    },
    { 
      productID: 9, 
      productName: "濃抹茶芒果千層", 
      price: 620, 
      purchaseQuantity: 0, 
      imgSrc: require('@/assets/12MatchaMangoCrepe(濃抹茶芒果千層)/469048.jpg'),
      alt: '濃抹茶芒果千層'
    },
    { 
      productID: 10, 
      productName: "鐵盒餅乾", 
      price: 480, 
      purchaseQuantity: 0, 
      imgSrc: require('@/assets/鐵盒餅乾/509226.jpg'),
      alt: '鐵盒餅乾'
    }
  ];
});

// 減少購買數量
const decreaseQuantity = (index) => {
  const product = allProducts.value[index];
  if (product.purchaseQuantity > 0) {
    product.purchaseQuantity--;
  }
};

// 增加購買數量
const increaseQuantity = (index) => {
  const product = allProducts.value[index];
  product.purchaseQuantity++;
};

// 計算總購買數量
const totalPurchaseQuantity = computed(() => {
  return allProducts.value.reduce((total, product) => total + product.purchaseQuantity, 0);
});

// 計算總價
const totalPrice = computed(() => {
  return allProducts.value.reduce((total, product) => total + (product.purchaseQuantity * product.price), 0);
});

// 提交訂單
const submitOrder = async () => {
  const order = allProducts.value.filter(product => product.purchaseQuantity > 0)
    .map(product => ({
      ID: product.productID,
      name: product.productName,
      quantity: product.purchaseQuantity
    }));

  alert(JSON.stringify(order, null, 2));
};
</script>



